<template>
  <div class="editContactModal">
    <button @click="closeModal" class="close-modal">
      <close-icon fillColor="#2c3e50" class="icon-button" />
    </button>
    <h1>{{ title }}</h1>
    <vue-form :state="formstate" @submit.prevent="onSubmit">
      <validate tag="label">
        <span>Contactnaam *</span>
        <input
          v-model="model.name"
          class="input__styling"
          required
          name="name"
        />

        <span>Naam in slack</span>
        <input
          v-model="model.slackName"
          class="input__styling"
          name="slackName"
        />

        <span>Email</span>
        <input v-model="model.mail" class="input__styling" name="mail" />

        <field-messages name="name">
          <div slot="required">Naam is een verplicht veld</div>
        </field-messages>
      </validate>

      <button type="submit">Submit</button>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "title", "cat_key", "mail", "slackName"],
  name: "edit-contact",
  data: function() {
    return {
      formstate: {},
      model: {
        name: this.name,
        slackName: this.slackName,
        mail: this.mail
      }
    };
  },
  methods: {
    onSubmit: function() {
      if (this.formstate.$invalid) {
        // alert user and exit early
        return;
      }
      this.$store.dispatch("editContact", {
        name: this.model.name,
        key: this.cat_key,
        slackName: this.model.slackName,
        mail: this.model.mail
      });
      this.$store.dispatch("setContacts");
      this.$emit("close");
    },
    closeModal: function() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.editContactModal {
  padding: 20px;
}
</style>
