<template>
  <div class="editCategoryModal">
    <button @click="closeModal" class="close-modal">
      <close-icon fillColor="#2c3e50" class="icon-button" />
    </button>
    <h1>{{ title }}</h1>
    <vue-form :state="formstate" @submit.prevent="onSubmit">
      <validate tag="label">
        <span>Categorienaam *</span>
        <input
          v-model="model.name"
          class="input__styling"
          required
          name="name"
        />

        <field-messages name="name">
          <div slot="required">Naam is een verplicht veld</div>
        </field-messages>
      </validate>

      <button type="submit">Submit</button>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "title", "cat_key"],
  name: "edit-category",
  data: function() {
    return {
      formstate: {},
      model: {
        name: this.name
      }
    };
  },
  methods: {
    onSubmit: function() {
      if (this.formstate.$invalid) {
        // alert user and exit early
        return;
      }
      this.$store.dispatch("editCategory", {
        name: this.model.name,
        key: this.cat_key
      });
      this.$store.dispatch("setCategories");
      this.$emit("close");
    },
    closeModal: function() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.editCategoryModal {
  padding: 20px;
}
</style>
